.float {
  position: fixed;
  width: 120px;
  height: 60px;
  bottom: 40px;
  right: 40px;
  background-color: rgb(255, 255, 255);
  color: #000000;
  border-radius: 50px;
  text-align: center;
  box-shadow: 2px 2px 3px #999;
  border-width: 0;
}